html{
	font-size:20px;
	padding:0;
	font-family: 'Advent Pro';
	
}

body{
	padding:0;
	background: #22d0ff;
	background-size: cover;
	font-size:4em;
	color:white;
	height:100%;
}
#root{
	height: 100%;
}

.setpointsDiv{
	width: 100%;
	height:35%;
	margin-top:5%;
	text-align: center;
	font-size: 40%;
	display: -ms-flexbox;
	display: flex;	
	-ms-flex-wrap: wrap;	
	    flex-wrap: wrap;
}
.setpoints{
	margin:0;
	width:100%;
}

#tempSetpoint{
	font-size: 3rem;		
}

#humiditySetpoint{
	font-size: 3rem;		
}
.chartContainer{
	width: 100%;
	height:40%;
}
#chart_div{
	width: 60%;
	height: 100%;
	margin-top:5%;
	margin-left:auto;
	margin-right:auto;
}
.roomsDiv{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
	font-size:3rem;
	font-family: 'Advent Pro';	
}
.rooms{
	width:20%;
	height: auto;	
	overflow:hidden;
	text-align: center;
	margin-left: 20px;
	margin-right: 20px; 	
	background: rgb(30,225,0);
	border-style: solid;
	border-color: transparent;
	border-width: 3px;
	border-radius: 5px;
	margin-bottom: 20px;

}
.rooms:hover{
	border-style: solid;
	border-width: 3px;
	border-color: white;
	border-radius: 5px;
	cursor: -webkit-grab;
	cursor: grab;
}
.rooms:active{
	border-style: solid;
	border-width: 3px;
	border-color: yellow;
	border-radius: 5px;

}
.roomsName{
	font-size: 1.5rem;
}
#addRoom{	
	background-color: rgb(0,30,200);
	opacity: 0.4;
}
#addRoom:active{
	opacity: 1.0;
}
#room2{
	background: rgb(10,240,255);	
}



